@keyframes loading-spinner-rolling-anim {
  0% { transform: translate(-50%,-50%) rotate(0deg); }
  100% { transform: translate(-50%,-50%) rotate(360deg); }
}

.loading-spinner-rolling-anim div {
  position: absolute;
  width: 80px;
  height: 80px;
  border: 15px solid #f9f9f9;
  border-top-color: transparent;
  border-radius: 50%;
}

.loading-spinner-rolling-anim div {
  animation: loading-spinner-rolling-anim 0.3921568627450981s linear infinite;
  top: 100px;
  left: 100px
}

.loading-spinner-rolling {
  position: relative;
  width: 200px;
  height: 200px;
  margin-top: 180px;
  display: inline-block;
  overflow: hidden;
  background: none;
}

.loading-spinner-rolling-anim {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0; /* see note above */
}

.loading-spinner-rolling-anim div { box-sizing: content-box; }
